      }
      const search = {
        psg: this.$store.state.choose_psg.concat(
          this.$store.state.choose_psg_more
        ),
        cabin: this.cabin,
        cities: this.cities,
        date: this.date_D,
        router_active: this.router_active,
        type: this.cabinType(this.cabin),
      };
      if (!this.date_D[0]) {
        this.$notify("请选择日期");
        return;
      }
      if (this.router_active == 1) {
        let a = this.date_D[0];
        let b = this.date_D[1];
        if (!a || !b) {
          this.$notify("请选择日期");
          return;
        }
        if (
          new Date(a.replace(/-/g, "/")).getTime() >
          new Date(b.replace(/-/g, "/")).getTime()
        ) {
          this.$$notify("返程日期应在出发日期后");
          return;
        }
      }
      if (!this.cities[0] || !this.cities[1]) {
        this.$notify("请选择城市");
        return;
      }
      if (this.$store.state.choose_psg.length == 0) {
        this.$notify("请选择乘机人");
        return;
      }
      sessionStorage.setItem("search_train", JSON.stringify(search));
      console.log('查询信息保存', search);
      this.$router.push("/trainticket");
    },
    cabinType (c) {
      console.log('座位类型', c);
      let cb = [
        "一等座",
        "二等座",
        "商务座",
        "硬座",
        "软座",
        "动卧",
        "高级动卧",
        "高级软卧",
        "硬卧",
        "特等座",
        "无座",
        "二等卧",
        "一等卧",
        "软卧",
      ];
      if (c == "一等座-First-Class Seat") {
        return cb[0];
      } else if (c == "二等座-Second-Class Seat") {
        return cb[1];
      } else if (c == "商务座 特等座-Business-Class Seat") {
        return cb[2];
      } else if (c == "硬座-Hard Seat") {
        return cb[3];
      } else if (c == "软卧-Soft Sleeper") {
        return cb[4];
      } else if (c == "动卧-Movable Lying") {
        return cb[5];
      } else if (c == "高级动卧-Advanced Mobile Sleeper") {
        return cb[6];
      } else if (c == "高级软卧-High-Grade Soft Berth") {
        return cb[7];
      } else if (c == "硬卧-Hard Sleeper") {
        return cb[8];
      } else if (c == "软座-Upholstered Seat") {
        return cb[13];
      } else if (c == "无座-Without Seat") {
        return cb[10];
      } else if (c == "二等卧/Second class sleeper") {
        return cb[11];
      } else if (c == "一等卧/First class berth") {
        return cb[12];
      }
    },
    get_back_Date (date) {
      let t = date.getTime() + 24 * 3600 * 1000;
      this.date_D[1] = new Date(t).format("yyyy-MM-dd");
      this.showPicker2 = false;
    },
  },
  computed: {
    // currentCity () {
    //   return this.cities[0] = this.cities[0];
    // },
    // currentCity1 () {
    //   return this.cities[1] = this.cities[1];
    // },
  },
  components: {
    psd_cont,
  },
  beforeRouteLeave (to, from, next) {
    if (to.path == '/index') {
      sessionStorage.removeItem('search_train')
    }
    next()

  }
};
</script>
